.examples {
  margin-top: 100px;
  padding-top: 100px;
  width: 90%;
  &__title {
    margin-top: 40px;
    text-align: center;
    font-size: 2.8rem;
  }
  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 40px;
  }
  &__item {
    max-height: 400px;
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
