.promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("/images/bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  width: 100%;
  padding: 40px;
  text-align: center;

  &__title {
    color: #000;
    font-size: 7.2rem;
  }
  &__text {
    margin-top: 20px;
    font-size: 3.6rem;
  }
  &__order {
    margin-top: 70px;
    color: #fff;
    font-weight: bold;
    font-size: 3.6rem;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #ff7878;
    border-radius: 15px;
  }
}
