.about {
  margin-top: 150px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 80px;
  background-color: #f2f2f2;
  &__text {
    padding-top: 80px;
    padding-left: 10%;
    padding-right: 10%;
  }
  &__pic {
    position: relative;
    min-height: 600px;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__title {
    font-weight: bold;
    font-size: 3.6rem;
  }
  &__desc {
    font-size: 2.4rem;
    margin-top: 40px;
    p {
      margin-bottom: 30px;
    }
  }
}
@media (min-width: 600px) {
  .about {
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  }
}
