.follow {
  margin-top: 140px;
  width: 100%;
  padding-bottom: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f2f2f2;
  &__title {
    margin-top: 40px;
    margin-bottom: 40px;
    font-size: 3.6rem;
    text-align: center;
  }
  &__link {
    color: #000;
    text-decoration: underline;
  }
  &__galery {
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 40px;
  }
  &__pic {
    min-height: 400px;
    &_big {
      grid-row: span 2;
    }
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
