* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1.2;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
html {
  font-family: "Nunito", sans-serif;
  font-size: 10px;
  scroll-behavior: smooth;
  width: 100%;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
