.advantages {
  margin-top: 150px;
  width: 90%;
  &__title {
    text-align: center;
  }
  &__list {
    margin-top: 80px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    width: 100%;
    gap: 40px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 100%;
    row-gap: 40px;
  }
  &__pic {
    width: 100%;
    min-height: 300px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    &_material {
      background-image: url("/images/material.jpg");
    }
    &_design {
      background-image: url("/images/bracer.jpg");
    }
    &_quality {
      background-image: url("/images/ok.jpg");
    }
  }
  &__title {
    font-size: 3.6rem;
  }
  &__desc {
    text-align: center;
    font-size: 2.4rem;
  }
}
